import React, { useEffect, useState } from 'react';
import Web3 from 'web3';
import config from '../Contract/Config.json';
import { injected } from '../Connector/Connectors';
import abi from '../Contract/abi.json';
import Web3EthContract from "web3-eth-contract";
import { useWeb3React } from '@web3-react/core';
import contract from '../Contract/contractInstance';
import dnsResolver from '@dogetaglord/dns-resolver';
import gif from '../images/pic.gif'

// import { useState } from 'react';

const Mint = () => {
  const { active, account, library, connector, activate, deactivate } = useWeb3React();
  const [networkID, setNetworkID] = useState(null);
  const [totalSupply, setTotalSupply] = useState(null);
  const [cost, setCost] = useState(null);
  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState("");
  const [mintAmount, setMintAmount] = useState(null);
  const [dnsName, setDnsName] = useState("")

  const mintNFT = () => {
    let gasLimit = config.gasLimit;
    let totalCostWei = String(cost * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);
    console.log("Cost: ", totalCostWei);
    console.log("Gas limit: ", totalGasLimit);
    setFeedback(`Minting your ${config.nftName}...`);
    setClaimingNft(true);
    contract.methods
      .mint(mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: config.contractAddress,
        from: account,
        value: Web3.utils.toWei(String(mintAmount * config.cost), "ether"),
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `WOW, the ${config.nftName} is yours! go visit Opensea.io to view it.`
        );
        setClaimingNft(false);
        // dispatch(fetchData(blockchain.account));
      });
  }

  const getNetworkId = async () => {
    const networkId = await window.ethereum.request({
      method: "net_version",
    });
    console.log("network id", networkId)
    setNetworkID(networkId)
  }

  const changeNetworkToDoge = async () => {
    await window.ethereum.request({
      method: 'wallet_addEthereumChain',
      params: [
        {
          chainId: '0x7D0',
          chainName: 'DogeChain',
          nativeCurrency: {
            name: 'DogeCHain',
            symbol: 'DOGE', // 2-6 characters long
            decimals: 18
          },
          blockExplorerUrls: ['https://dogechain.info/chain/Dogecoin'],
          rpcUrls: ['https://rpc01-sg.dogechain.dog/'],
        },
      ],
    });


    await window.ethereum.request({  // don't use send()
      method: "eth_requestAccounts"
    });
  }

  const getPrice = async () => {
    let mintPrice = await contract.methods.PRICE()
      .call()
      .then((res) => {
        console.log("res", res);
        setCost(res)
      });
  }
  const getTotalSupply = async () => {
    let totalSupply = await contract.methods.totalSupply()
      .call()
      .then((res) => {
        console.log("res", res);
        setTotalSupply(res);
      });
  }
  // const enableTransactionAccess = async () => {
  //   const accounts = await window.ethereum.request({
  //         method: "eth_requestAccounts",
  //       });
  // }
  // const getDNSName = async () => {
  //   let name = await dnsResolver.getPrimaryName(window.ethereum, account)
  //   console.log("name", name)
  //   setDnsName(name)
  // }
  useEffect(() => {
    if (account) {
      // let web3 = new Web3(window.ethereum)

      // let contract = new web3.eth.Contract(abi, config.contractAddress, {
      //   from: account
      // })

      // let contract = new Web3EthContract(
      //   abi,
      //   config.contractAddress
      // );
      console.log(contract)
      getNetworkId()
      if (networkID == config.networkId) {
        console.log("right network")
        // enableTransactionAccess()
      }
      else {
        changeNetworkToDoge()
        // enableTransactionAccess()

      }
      getTotalSupply()
      getPrice();
      // getDNSName();
    }

  }, [networkID, account])

  return (
    <div
      style={{
        display: 'flex',
        flexDirection:'column',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '90vh',
        height:'auto',
        background:'#FFE600',paddingTop:100,paddingBottom:200
      }}
    >
      
      {account ?
        <>
          <img style={{width:'300px'}} src={gif}/>
          <br/>
          <p>{mintAmount}</p>
          <br/>
          <p style={{textAlign:'center',color:'PURPLE'}}>One BabyPixel Cost: 40 Doge</p>
          <br/>
          <p>{feedback}</p>
          <br/> 
          <input
            placeholder='Enter Mint Amount'
            className='inputMintBox'
            style={{padding:"10px 15px"}}
            onChange={(e) => {
              setMintAmount(e.target.value)
            }}
          >
          </input>
          <br/>
          <button style={{padding:'20px',background:'white',border:'none'}} onClick={(mintAmount>0)?mintNFT:console.log("none")}>Mint</button>
          <p>{dnsName}</p>
          <p>{feedback}</p>
        </>
        : <p style={{color:'PURPLE'}}>connect wallet</p>}
    </div>
  );
};

export default Mint;
