import React from "react";
import {
  VerticalTimeline,
  VerticalTimelineElement
} from "react-vertical-timeline-component";
// import SchoolIcon from "@material-ui/icons/School";
// import WorkIcon from "@material-ui/icons/Work";
// import StarIcon from "@material-ui/icons/Star";
import "react-vertical-timeline-component/style.min.css";
// import "./styles.css";

export default function Timeline() {
  return (
    <div style={{background:'#F771A6',paddingTop:80}}>
      <h1 style={{textAlign:'center'}}>Roadmap</h1>
      <br/>
      <br/>
      <VerticalTimeline>
        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          contentStyle={{ background: "white", color: "black" }}
          contentArrowStyle={{ borderRight: "7px solid  white" }}
          date="20% Sold"
          iconStyle={{ background: "black", color: "#fff" }}
          icon={<p></p>}
        >
          <h3 className="vertical-timeline-element-title">NFT Giveaway</h3>
          {/* <h4 className="vertical-timeline-element-subtitle">Miami, FL</h4> */}
          <p style={{color:'purple'}}>
          A random airdrop of 20 NFTs to early community members in our Telegram.
          </p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          date="50% Sold"
          iconStyle={{ background: "black", color: "#fff" }}
          icon={<p></p>}
        >
          <h3 className="vertical-timeline-element-title">SmolPunks 10x Baby Bonus</h3>
          {/* <h4 className="vertical-timeline-element-subtitle">
            San Francisco, CA
          </h4> */}
          <p style={{color:'purple'}}>
          On the 388.5th sale, there will be a payout 10x the mint price in $wDOGE to 4 community members in our Telegram.
          </p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          date="90% Sold"
          iconStyle={{ background: "black", color: "#fff" }}
          icon={<p></p>}
        >
          <h3 className="vertical-timeline-element-title">SmolPunks Merch</h3>
          {/* <h4 className="vertical-timeline-element-subtitle">
            Los Angeles, CA
          </h4> */}
          <p style={{color:'purple'}}>
          SmolPunks Merch store will be rolled out where NFT holders will get first dibs and exclusive access to limited edition t-shirts, hats, and hoodies. Non-NFT holders will be given access at a later date.
                    </p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          date="100% Sold"
          iconStyle={{ background: "black", color: "#fff" }}
          icon={<p></p>}
        >
          <h3 className="vertical-timeline-element-title">SmolPunks Sweeper</h3>
          {/* <h4 className="vertical-timeline-element-subtitle">
            San Francisco, CA
          </h4> */}
          <p style={{color:'purple'}}>A liquidity wallet will be created that will be used to buy the floor of SmolPunks NFTs at a TBD time and when available on a marketplace. </p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          className="vertical-timeline-element--education"
          date="100% Sold"
          iconStyle={{ background: "black", color: "#fff" }}
          icon={<p></p>}
        >
          <h3 className="vertical-timeline-element-title">
          The PUNK-A-TRON Arcade
          </h3>
          {/* <h4 className="vertical-timeline-element-subtitle">Online Course</h4> */}
          <p style={{color:'purple'}}>
          We will build an even bigger arcade than we planned for previously that will host approximately 10 titles by the end of this year. The games will feature retro old school classics. All NFT holders will be able to stake their NFTs to earn SmolPunk Tokens from transaction fees paid by players to play the games to compete head to head for prizes such popular NFTs, and Doge tokens.
          </p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          className="vertical-timeline-element--education"
          date="Phase 2"
          iconStyle={{ background: "black", color: "#fff" }}
          icon={<p></p>}
        >
          <h3 className="vertical-timeline-element-title">
          TeenPunks
          </h3>
          {/* <h4 className="vertical-timeline-element-subtitle">Certification</h4> */}
          <p style={{color:'purple'}}>
          We will create a limited collection of stylized TeenPunks and airdrop them to all holders.
                    </p>
        </VerticalTimelineElement>
        
        <VerticalTimelineElement
          iconStyle={{ background: "green", color: "#fff" }}
          icon={<p></p>}
        />
      </VerticalTimeline>
    </div>
  );
}
