import React from 'react';
import {
  Nav,
  NavLink,
  Bars,
  NavMenu,
  NavBtn,
  NavBtn2,
  NavBtnLink
} from './NavbarElements';
import { injected } from '../../Connector/Connectors';
import { useWeb3React } from '@web3-react/core';
import { useState } from 'react';



const Navbar = (props) => {
  const { active, account, library, connector, activate, deactivate } = useWeb3React();
  const [modal, showModal] = useState(false)
  async function connect() {
    try {
      console.log("connect function try block")
      await activate(injected)
    } catch (ex) {
      console.log(ex)
    }
  }
  async function disconnect() {
    try {
      console.log("disconnect function try block")
      await deactivate()
    } catch (ex) {
      console.log(ex)
    }
  }
  return (
    <>
      <Nav>
        <NavLink to='/'>
          <img style={{ height: 60 }} src={require('../../images/smolpunks.png')} alt='logo' />
        </NavLink>
        <Bars onClick={() => {
          showModal(!modal)
        }} />

        <NavMenu>
          <NavLink to='/home' activeStyle>
            Home
          </NavLink>
          <NavLink to='/mint' activeStyle>
            Mint
          </NavLink>
          <NavLink to='/gallery' activeStyle>
            Gallery
          </NavLink>

          {/* Second Nav */}
          {/* <NavBtnLink to='/sign-in'>Sign In</NavBtnLink> */}
        </NavMenu>
        {active ? (
          <>
            {/* <p style={{ color: 'white',fontSize:14 }} onClick={disconnect}>{account.slice(0, 5) + "..." + account.slice(-4, -1)}</p> */}
            <NavBtn>
              <p style={{ color: 'black' }} onClick={disconnect}>Disconnect</p>
            </NavBtn>
          </>
        )
          :
          <NavBtn>
            <p style={{ color: 'black' }} onClick={connect}>Connect</p>
          </NavBtn>
        }

      </Nav>
      {modal ?
        <div style={{ position: 'absolute', background: 'black', width: '100vw', height: 700, zIndex: 999 }}>
          {/* <NavMenu> */}

          <div style={{ padding: '30px 20px' }}>
            <NavLink to='/home' activeStyle>
              Home
            </NavLink>
            <br />
            <br />
            <NavLink to='/mint' activeStyle>
              Mint
            </NavLink>
            <br />
            <br />
            <NavLink to='/gallery' activeStyle>
              Gallery
            </NavLink>
            <br />
            {active ? (
              <>
                {/* <p style={{ color: 'white',fontSize:14 }} onClick={disconnect}>{account.slice(0, 5) + "..." + account.slice(-4, -1)}</p> */}
                <NavBtn2>
                  <p style={{ color: 'black' }} onClick={disconnect}>Disconnect</p>
                </NavBtn2>
              </>
            )
              :
              <NavBtn2>
                <p style={{ color: 'black' }} onClick={connect}>Connect</p>
              </NavBtn2>
            }
          </div>

          {/* Second Nav */}
          {/* <NavBtnLink to='/sign-in'>Sign In</NavBtnLink> */}
          {/* </NavMenu> */}
        </div>
        : ""}
    </>
  );
};

export default Navbar;
